import polyfills from './polyfills';
import detectTouch from './detectTouch';
import portfolioSlider from './portfolioSlider';
import pricesTabs from './pricesTabs';
import partnersSlider from './partnersSlider';
import menuAndModals from './menuAndModals';
import introTextAnimation from './introTextAnimation';
import scrollAnimations from './scrollAnimations';
import stickyElements from './stickyElements';
import introBlockMenuOpen from './introBlockMenu';
import blogPageFilters from './blogPageFilters';
// import smoothBodyScrolling from './smoothBodyScrolling';
import inputMasking from './inputMasking';
import './midnight';
import mobileHeader from './mobileHeader';
import articlesShowMore from './articlesShowMore';
import form from './form';

document.addEventListener('DOMContentLoaded', function() {
    // introTextAnimation();
    polyfills();
    detectTouch();
    mobileHeader();
    portfolioSlider();
    pricesTabs();
    partnersSlider();
    menuAndModals();
    scrollAnimations();
    stickyElements();
    introBlockMenuOpen();
    blogPageFilters();
    inputMasking();
    articlesShowMore();
    form();
    
});

window.addEventListener('load', function() {
    document.body.classList.add('loaded');
    setTimeout(() => {
        introTextAnimation();
    }, 300);
})
