export default function() {
    const blogPages = Array.from(document.querySelectorAll('.js-blog-page'));

    blogPages.forEach(page => {
        const filtersBtns = Array.from(page.querySelectorAll('.js-blog-filter-btn'));
        const items = Array.from(page.querySelectorAll('.blog-page__cards-list-item'));

        function filterBy(category) {
            console.log('Filtering for', category);
            if (category === 'all') {
                items.forEach(item => item.classList.remove('hidden'));
                return;
            }
            items.forEach(item => {
                if (item.getAttribute('data-category') === category) {
                    item.classList.remove('hidden');
                } else {
                    item.classList.add('hidden');
                }
            });
        }

        filtersBtns.forEach(btn => {
            btn.addEventListener('click', event => {
                event.preventDefault();
                filtersBtns.forEach(btn => btn.classList.remove('active'));
                btn.classList.add('active');
                filterBy(btn.getAttribute('data-filter'));
            });
        });
    });
}
