export default function() {
    const introBlockMenuOpen = document.querySelector('.intro-block__open-menu');
    const introBlockMenu = document.querySelector('.intro-block__side-menu');
    const introBlockMenuClose = document.querySelector('.intro-block__side-menu-link--close');

    if (introBlockMenuOpen) introBlockMenuOpen.addEventListener('click', event => {
        event.preventDefault();
        document.body.classList.add('intro-social-menu-open');
    })

    if (introBlockMenuClose) introBlockMenuClose.addEventListener('click', event => {
        event.preventDefault();
        document.body.classList.remove('intro-social-menu-open');
    })
}