export default function() {
    const showMore = document.querySelector('.js-articles-show-more');

    if (showMore) {
        showMore.addEventListener('click', event => {
            event.preventDefault();
            const cardsContainer = showMore.closest('.blog-page__cards');

            if (cardsContainer) {
                cardsContainer.classList.add('show-all');
            }
        })
    }
}