import 'parsleyjs';

export default function () {
    window.Parsley.addValidator('phone', {
        requirementType: 'string',
        validateString: function (value) {
            const valueCleared = value.replace(/ /g, '');
            console.log('Validating value', valueCleared);
            return /^\+[1-9]{1}[0-9]{3,14}$/.test(valueCleared);
        },
        messages: {
            en: 'This value should be a mobile number',
            ru: '    ',
        },
    });

    Parsley.addMessages('ru', {
        defaultMessage: ' .',
        type: {
            email: '      E-mail',
            url: '   .',
            number: ' .',
            integer: '  .',
            digits: '  .',
            alphanum: ' - .',
        },
        notblank: '    .',
        required: ' ',
        pattern: '  .',
        min: '       %s.',
        max: '       %s.',
        range: '     %s  %s.',
        minlength: '      %s .',
        maxlength: '      %s .',
        length: '     %s  %s .',
        mincheck: '   %s .',
        maxcheck: '   %s .',
        check: '  %s  %s .',
        equalto: '   .',
    });

    Parsley.setLocale('ru');
}
