import Swiper from 'swiper';

export default function() {
    const portfolioSliders = Array.from(document.querySelectorAll('.js-portfolio-slider'));

    portfolioSliders.forEach(slider => {
        const container = slider.querySelector('.swiper-container');

        const swiperInstance = new Swiper(container, {
            slidesPerView: 'auto',
            watchOverflow: true,
            spaceBetween: 30,
            centeredSlides: true,
            slideToClickedSlide: true,
            navigation: {
                nextEl: slider.querySelector('.portfolio__slider-next-btn'),
                prevEl: slider.querySelector('.portfolio__slider-prev-btn')
            },
            breakpoints: {
                769: {
                    spaceBetween: 100,
                    centeredSlides: false
                }
            }
        });

        swiperInstance.snapGrid = [...swiperInstance.slidesGrid];
    });
}
