import { lockScroll, unlockScroll } from './scrollBlocker';
import jump from 'jump.js';
// import PerfectScrollbar from 'perfect-scrollbar';
// import detectIt from 'detect-it';

export default function() {
    

    const scrollableMenuWrapper = document.querySelector('.page-header__mobile-nav-inner');

    function closeMenu() {
        document.body.classList.remove('menu-open');
        unlockScroll();
    }

    function openMenu() {
        document.body.classList.add('menu-open');
        lockScroll(scrollableMenuWrapper);
    }

    function handleMenu() {
        const menuOpen = document.body.classList.contains('menu-open');
        if (menuOpen) {
            closeMenu();
        } else {
            openMenu();
        }
    }

    document.addEventListener('click', event => {
        const isBurger = event.target.matches('.page-header__burger') || event.target.closest('.page-header__burger');

        if (isBurger) {
            handleMenu();
        }
    });

    const windowHash = window.location.hash;

    console.log('Window hash', windowHash);

    const anchors = Array.from(document.querySelectorAll('.js-anchor-link'));
    let jumpAnimating = false;
    anchors.forEach(anchor => {
        anchor.addEventListener('click', event => {
            event.preventDefault();
            if (jumpAnimating) return;
            const hash = anchor.hash;
            const element = document.querySelector(hash);

            if (element) {
                closeMenu();
                jumpAnimating = true;
                jump(element, {
                    callback: () => {
                        jumpAnimating = false;
                    }
                });
            }
        });
    });

    if (windowHash) {
        const elementToScroll = document.querySelector(windowHash);
        if (elementToScroll) {
            event.preventDefault();
            if (jumpAnimating) return;

            jumpAnimating = true;
            jump(elementToScroll, {
                callback: () => {
                    jumpAnimating = false;
                }
            });
        }
    }

    const scrollDownBtn = document.querySelector('.intro-block__scroll-down');

    if (scrollDownBtn)
        scrollDownBtn.addEventListener('click', event => {
            event.preventDefault();
            window.scrollBy({
                top: window.innerHeight,
                behavior: 'smooth'
            });
        });

    function modalsController(modals) {
        if (!Array.isArray(modals)) throw new TypeError('Modals array required');
        let activeModalIndex = 0;
        const modalsCount = modals.length;

        function openModal(id) {
            const modalToOpenIndex = modals.findIndex(element => {
                if (element.id.toLowerCase().trim() === id.toLowerCase().trim()) {
                    return true;
                } else {
                    return false;
                }
               
            });
            if (modalToOpenIndex === -1) {
                console.log('Modal not found');
                return false;
            }
            activeModalIndex = modalToOpenIndex;

            const modalToOpen = modals[activeModalIndex];

            closeMenu();
            modals.forEach(modal => modal.classList.add('active-state'))
            modalToOpen.classList.add('shown');
           

            const modalInner = modalToOpen.querySelector('.modal__inner');
            const transitionEndHandler = () => {
                lockScroll(modalInner);
                modalToOpen.removeEventListener('transitionend', transitionEndHandler)

            }

            modalToOpen.addEventListener('transitionend', transitionEndHandler)
            
        }

        function closeModal(removeActiveState=true) {
            unlockScroll();
            const modalToClose = modals[activeModalIndex];
           


            if (removeActiveState) {
                modals.forEach(modal => modal.classList.remove('active-state'));

                setTimeout(() => {
                    modalToClose.classList.remove('shown');
                }, 50);
            } else {
                modalToClose.classList.remove('shown');
            }
           
        }

        function nextModal() {
            if (activeModalIndex + 1 === modalsCount) {
                closeModal(false);
                openModal(modals[0].id);
            } else {
                closeModal(false);
                openModal(modals[activeModalIndex + 1].id);
            }
        }

        function previousModal() {
            if (activeModalIndex === 0) {
                closeModal(false);
                openModal(modals[modalsCount - 1].id);
            } else {
                closeModal(false);
                openModal(modals[activeModalIndex - 1].id);
            }
        }

        function addListeners() {
            modals.forEach(item => {
                const closeBtns = Array.from(item.querySelectorAll('.js-modal-close'));
                const prevBtns = Array.from(item.querySelectorAll('.js-modal-prev'));
                const nextBtns = Array.from(item.querySelectorAll('.js-modal-next'));

                closeBtns.forEach(close => {
                    close.addEventListener('click', event => {
                        event.preventDefault();
                        closeModal();
                    });
                });

                prevBtns.forEach(prev => {
                    prev.addEventListener('click', event => {
                        event.preventDefault();
                        previousModal();
                    });
                });

                nextBtns.forEach(next => {
                    next.addEventListener('click', event => {
                        event.preventDefault();
                        nextModal();
                    });
                });
            });
        }

        addListeners();

        return {
            openModal,
            nextModal,
            previousModal,
            closeModal
        };
    }

    const servicesModals = Array.from(document.querySelectorAll('.js-services-modal'));

    const servicesModalsController = modalsController(servicesModals);

    const servicesModalOpenBtns = Array.from(document.querySelectorAll('.js-services-modal-open'));

    servicesModalOpenBtns.forEach(btn =>
        btn.addEventListener('click', event => {
            event.preventDefault();
            const hash = btn.hash.replace('#', '');
            console.log('Hash', hash);

            servicesModalsController.openModal(hash);
        })
    );

    const portfolioModals = Array.from(document.querySelectorAll('.js-modal-portfolio'));

    const portfolioModalsController = modalsController(portfolioModals);

    const portfolioModalOpenBtns = Array.from(document.querySelectorAll('.js-portfolio-modal-open'));

    console.log('Portfolio modal open', portfolioModalOpenBtns);

    portfolioModalOpenBtns.forEach(btn =>
        btn.addEventListener('click', event => {
            event.preventDefault();
            const hash = btn.hash.replace('#', '');
            console.log('Hash', hash);

            portfolioModalsController.openModal(hash);
        })
    );



    // const modals = Array.from(document.querySelectorAll('.modal__inner'));

    // if (!detectIt.hasTouch) {
    //     modals.forEach(modal => {
    //         new PerfectScrollbar(modal, {
    //             wheelSpeed: 2,
    //             wheelPropagation: false,
    //             minScrollbarLength: 20
    //         });
    //     });
    // }


}
