import anime from 'animejs/lib/anime.es.js';

export default function() {
    anime({
        targets: '.intro-block__hand-image-frame path',
        strokeDashoffset: [anime.setDashoffset, 0],
        opacity: [0, 1],
        easing: 'easeInOutSine',
        duration: 2000,
        delay: function(el, i) {
            return i * 250;
        },
        direction: 'normal',
        loop: false
    });

    if (!document.querySelector('.js-intro-text')) return;

    var textWrappers = Array.from(document.querySelectorAll('.js-intro-text .letters'));

    textWrappers.forEach(wrapper => {
        wrapper.innerHTML = wrapper.textContent.replace(/\S/g, "<span class='letter'>$&</span>");
    });

    anime.timeline({ loop: false }).add({
        targets: '.js-intro-text',
        opacity: [0, 1],
        easing: 'easeOutExpo',
        duration: 400
    });

    const textAnimation = anime
        .timeline({ loop: true })
        .add({
            targets: '.js-intro-text .text-block-1 .letter',
            translateX: [40, 0],
            translateZ: 0,
            opacity: [0, 1],
            easing: 'easeOutExpo',
            duration: 1200,
            delay: (el, i) => 500 + 30 * i
        })
        .add({
            targets: '.js-intro-text .text-block-1 .letter',
            translateX: [0, -30],
            opacity: [1, 0],
            easing: 'easeInExpo',
            duration: 1100,
            delay: (el, i) => 100 + 30 * i
        })
        .add({
            targets: '.js-intro-text .text-block-2 .letter',
            translateX: [40, 0],
            translateZ: 0,
            opacity: [0, 1],
            easing: 'easeOutExpo',
            duration: 1200,
            delay: (el, i) => 500 + 30 * i
        })
        .add({
            targets: '.js-intro-text .text-block-2 .letter',
            translateX: [0, -30],
            opacity: [1, 0],
            easing: 'easeInExpo',
            duration: 1100,
            delay: (el, i) => 100 + 30 * i
        })
        .add({
            targets: '.js-intro-text .text-block-3 .letter',
            translateX: [40, 0],
            translateZ: 0,
            opacity: [0, 1],
            easing: 'easeOutExpo',
            duration: 1200,
            delay: (el, i) => 500 + 30 * i
        })
        .add({
            targets: '.js-intro-text .text-block-3 .letter',
            translateX: [0, -30],
            opacity: [1, 0],
            easing: 'easeInExpo',
            duration: 1100,
            delay: (el, i) => 100 + 30 * i
        })
        .add({
            targets: '.js-intro-text .text-block-4 .letter',
            translateX: [40, 0],
            translateZ: 0,
            opacity: [0, 1],
            easing: 'easeOutExpo',
            duration: 1200,
            delay: (el, i) => 500 + 30 * i
        })
        .add({
            targets: '.js-intro-text .text-block-4 .letter',
            translateX: [0, -30],
            opacity: [1, 0],
            easing: 'easeInExpo',
            duration: 1100,
            delay: (el, i) => 100 + 30 * i
        })
        .add({
            targets: '.js-intro-text .text-block-5 .letter',
            translateX: [40, 0],
            translateZ: 0,
            opacity: [0, 1],
            easing: 'easeOutExpo',
            duration: 1200,
            delay: (el, i) => 500 + 30 * i
        })
        .add({
            targets: '.js-intro-text .text-block-5 .letter',
            translateX: [0, -30],
            opacity: [1, 0],
            easing: 'easeInExpo',
            duration: 1100,
            delay: (el, i) => 100 + 30 * i
        })

    window.addEventListener('resize', () => {
        textAnimation.restart();
    });
}
