export default function() {
    const pricesTabs = Array.from(document.querySelectorAll('.js-prices-tabs'));

    pricesTabs.forEach(tabContainer => {
        const btns = Array.from(tabContainer.querySelectorAll('.prices__dev-tariffs-nav-btn'));
        const items = Array.from(tabContainer.querySelectorAll('.prices__dev-tariffs-tab-item'));

        function changeTab(index) {
            btns.forEach(item => item.classList.remove('active'));
            items.forEach(item => item.classList.remove('active'));

            btns[index].classList.add('active');
            items[index].classList.add('active');
        }

        changeTab(0);

        btns.forEach((btn, index) => {
            btn.addEventListener('click', event => {
                event.preventDefault();
                changeTab(index);
            })
        })
    })
}