import Swiper from 'swiper';

export default function() {
    const partnersSliders = Array.from(document.querySelectorAll('.js-partners-slider'));

    partnersSliders.forEach(item => {
        const container = item.querySelector('.swiper-container');

        new Swiper(container, {
            slidesPerView: 'auto',
            watchOverflow: true,
            spaceBetween: 30,
            centeredSlides: true,
            navigation: {
                nextEl: item.querySelector('.partners__slider-next-btn'),
                prevEl: item.querySelector('.partners__slider-prev-btn')
            },
            breakpoints: {
                769: {
                    spaceBetween: 30,
                    centeredSlides: false
                }
            }
        });
    });
}
