import anime from 'animejs/lib/anime.es.js';
import ScrollMagic from 'ScrollMagic';
import Sticky from 'sticky-js';

export default function() {
    function animatePromo() {
        anime({
            targets: '.prices__promo-tariffs-frame path',
            strokeDashoffset: [anime.setDashoffset, 0],
            easing: 'easeInOutSine',
            duration: 2000,
            delay: function(el, i) {
                return i * 250;
            },
            direction: 'normal',
            loop: false
        });
    }

    const controller = new ScrollMagic.Controller();

    const pricesElement = document.querySelector('#prices');
    const contactsElement = document.querySelector('#contacts');

    if (pricesElement) {
        new ScrollMagic.Scene({
            triggerElement: pricesElement,
            triggerHook: 1,
            reverse: false
        })
            .on('start', function() {
                console.log('passed trigger');
                animatePromo();
            })
            .addTo(controller);
    }

    if (contactsElement) {
        new ScrollMagic.Scene({
            triggerElement: contactsElement,
            triggerHook: 0.1,
            reverse: true,
            duration: contactsElement.offsetHeight
        })
            .setClassToggle('body', 'contacts-revealed')
            .addTo(controller);
    }

    const isFirefox = typeof InstallTrigger !== 'undefined';

    if (isFirefox) {
        new Sticky('.js-main-nav-sticky');
        new Sticky('.services__connect-btn-placeholder');
    } else {
        const mainNavSticky = document.querySelector('.js-main-nav-sticky');

        if (mainNavSticky) {
            new ScrollMagic.Scene({
                triggerElement: mainNavSticky,
                triggerHook: 0
            })
                .setPin(mainNavSticky, {
                    pushFollowers: false
                })
                .setClassToggle(mainNavSticky, 'main-nav--sticky')
                .addTo(controller);
        }
        const servicesConnectBtn = document.querySelector('.js-sticky-connect');

        if (servicesConnectBtn) {
            new ScrollMagic.Scene({
                triggerElement: servicesConnectBtn,
                triggerHook: 0.15
            })
                .setPin(servicesConnectBtn, {
                    pushFollowers: false
                })
                .setClassToggle(servicesConnectBtn, 'sticky')
                .addTo(controller);
        }
    }
}
